import { useContext, useRef } from 'react';
import { Helmet } from 'react-helmet';
import { AppContext } from '../../../context/AppContext';
import { titlePageGenerator } from '../../../utils';
import Menu3D from '../../organisms/Menu3D/Menu3D';
import Navigation3D from '../../organisms/Navigation3D/Navigation3D';
import image from './expo-background.png';
import ScholarInter from './ScholarInter';
import classes from './ExpoHall.module.css';
import Group1x1 from './Group1x1';
import Group2x1 from './Group2x1';
import TransitionLayer from '../../atoms/TransitionLayer/TransitionLayer';
import useOnLoadImages from '../../../hooks/useOnLoadImages';

export default function ExpoHall() {
  const ctx = useContext(AppContext);
  const wrapperRef = useRef<HTMLDivElement>(null);
  const imagesLoaded = useOnLoadImages(wrapperRef);

  return (
    <>
      <Helmet>
        <title>{titlePageGenerator('Expo Hall')}</title>
      </Helmet>
      {ctx.showMenu3D && <Menu3D />}
      <div className="relative h-screen overflow-hidden" ref={wrapperRef}>
        <img
          src={image}
          alt=""
          className="absolute top-0 left-0 -z-10 h-screen w-full object-cover shadow-lg"
        />
        <Navigation3D href="/lobby" />
        <div className="flex h-screen w-full flex-col items-center justify-end">
          <div className="flex h-[67vh] w-1/2  flex-col justify-start bg-purple-500/0">
            <ScholarInter />
            <div
              title="divider"
              className={`w-full bg-red-500/0 ${classes.firstDividerHeight}`}
            />
            <Group1x1 />
            <div
              title="divider"
              className={`w-full bg-red-500/0 ${classes.thirdDividerHeight}`}
            />
            <Group2x1 />
          </div>
          <div className="h-[6vh] w-1/2" />
        </div>
      </div>
      {!imagesLoaded && <TransitionLayer showTransition={false} />}
    </>
  );
}
