import booth2x1companies from '../../../data/companies/Booth2x1';
import CompanyThumbCard from './CompanyThumbCard';

export default function Group2x1() {
  return (
    <div className="ml-[10px] flex h-[26vh] flex-wrap justify-center gap-[1vw]">
      {booth2x1companies.map((company, index) => (
        <CompanyThumbCard
          hSize="6.5vh"
          wSize="7vw"
          className={index === booth2x1companies.length - 2 ? 'ml-3' : ''}
          key={company.id}
          companyDetail={company}
        />
      ))}
    </div>
  );
}
