import {
  boothInternshipLine1,
  boothInternshipLine2,
  boothScholarshipLine1,
  boothScholarshipLine2,
} from '../../../data/companies/Booth1x1';
import CompanyThumbCard from './CompanyThumbCard';

export default function ScholarInter() {
  return (
    <div className="flex h-[11vh] w-full justify-between gap-[4.2vw]">
      <div className="h-[11vh] flex-1 bg-cyan-600/0">
        <div className="flex justify-end gap-[0.5vw]">
          {boothScholarshipLine1.map((company, i) => (
            <CompanyThumbCard
              hSize="4vh"
              wSize="2.6vw"
              companyDetail={company}
              key={company.id}
            />
          ))}
        </div>
        <div title="divider" className="h-[2.7vh] w-full bg-red-500/0" />
        <div className="flex justify-end gap-[0.7vw]">
          {boothScholarshipLine2.map((company, i) => (
            <CompanyThumbCard
              hSize="4vh"
              wSize="2.7vw"
              companyDetail={company}
              key={company.id}
            />
          ))}
        </div>
      </div>
      <div className="-ml-2.5 h-[11vh] flex-1 bg-cyan-600/0">
        <div className="flex justify-start gap-[0.5vw]">
          {boothInternshipLine1.map((_, i) => (
            <CompanyThumbCard
              hSize="4vh"
              wSize="2.6vw"
              companyDetail={_}
              key={_.id}
            />
          ))}
        </div>
        <div title="divider" className="h-[2.8vh] w-full bg-red-500/0" />
        <div className="flex justify-start gap-[0.8vw]">
          {boothInternshipLine2.map((_, i) => (
            <CompanyThumbCard
              hSize="4vh"
              wSize="2.6vw"
              companyDetail={_}
              key={_.id}
            />
          ))}
        </div>
      </div>
    </div>
  );
}
