import {
  booth1x1companiesLine1,
  booth1x1companiesLine2,
} from '../../../data/companies/Booth1x1';
import CompanyThumbCard from './CompanyThumbCard';
import classes from './ExpoHall.module.css';

export default function Group1x1() {
  return (
    <>
      <div className="flex h-[5vh] w-full gap-[1.1vw] bg-amber-100/0 pl-2">
        <div className="w-[1vh]" />
        {booth1x1companiesLine1.map((company, i) => (
          <CompanyThumbCard
            show={i === 1}
            companyDetail={company}
            hSize="5vh"
            wSize="2.75vw"
          />
        ))}
      </div>
      <div
        title="divider"
        className={`w-full bg-red-500/0 ${classes.secondDividerHeight}`}
      />
      <div className="flex h-[5.5vh] w-full gap-[1.35vw] bg-amber-100/0 pl-2">
        {booth1x1companiesLine2.map((company, i) => (
          <CompanyThumbCard
            companyDetail={company}
            hSize="5.5vh"
            wSize="2.78vw"
          />
        ))}
      </div>
    </>
  );
}
